import { getFormStatus } from "@/helpers/FormsHelpers";
import { I18n } from "@/constants/I18nConstants";

export const datesModule = {
  namespaced: true,
  state: {
    startReleaseDate: undefined,
    startDevelopmentDate: undefined,
  },
  getters: {
    getLocalize(state, value, rootState, rootGetters) {
      const localize = rootGetters["i18n/localize"];

      return {
        title: localize(I18n.DatesForm.Title),
        releaseDateLabel: localize(I18n.DatesForm.ReleaseDateLabel),
        releaseDateLabelPopoverText: localize(
          I18n.DatesForm.ReleaseDateLabelPopoverText
        ),
        developmentDateLabel: localize(I18n.DatesForm.DevelopmentDateLabel),
        developmentDateLabelPopoverText: localize(
          I18n.DatesForm.DevelopmentDateLabelPopoverText
        ),
      };
    },
    getStartReleaseDate(state) {
      return state.startReleaseDate;
    },
    getStartDevelopmentDate(state) {
      return state.startDevelopmentDate;
    },
    getFormStatus(state) {
      const hasReleaseDate = !!state.startReleaseDate;
      const hasDevelopmentDate = !!state.startDevelopmentDate;

      const isFilled = hasReleaseDate && hasDevelopmentDate;
      const isNotFilled = !hasReleaseDate && !hasDevelopmentDate;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    changeReleaseDate(state, date) {
      state.startReleaseDate = date;
    },
    changeDevelopmentDate(state, date) {
      state.startDevelopmentDate = date;
    },
    setForm(state, data) {
      state.startReleaseDate = data.startReleaseDate;
      state.startDevelopmentDate = data.startDevelopmentDate;
    },
  },
};
