import { I18n } from "@/constants/I18nConstants";

export const applicationModule = {
  namespaced: true,
  state: {
    language: "ru",
  },
  getters: {
    getApplicationLanguage(state) {
      return state.language;
    },
    getWeekdays(state, value, rootState, rootGetters) {
      const localize = rootGetters["i18n/localize"];

      return [
        localize(I18n.WeekdaysShort.Monday),
        localize(I18n.WeekdaysShort.Tuesday),
        localize(I18n.WeekdaysShort.Wednesday),
        localize(I18n.WeekdaysShort.Thursday),
        localize(I18n.WeekdaysShort.Friday),
        localize(I18n.WeekdaysShort.Saturday),
        localize(I18n.WeekdaysShort.Sunday),
      ];
    },
  },
  mutations: {
    changeApplicationLanguage(state, language) {
      state.language = language;
    },
  },
};
